  max-height: 100%;
}
.form-group {
  display: block;
  width: 100%;
}
.form-group label {
  text-align: right;
  text-align: right;
  line-height: 34px;
  vertical-align: baseline;
  margin: 0px;
  min-width: 121px;
}
input.form-control {
  background-color: #303030;
  color: white;
  border-color: #141414;
  display: -webkit-inline-box;
}
input.form-control.invalid {
  border-color: #d9534f;
  outline: 0;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(217, 83, 79, 0.6);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(217, 83, 79, 0.6);
}
input.form-control.valid {
  border-color: #5cb85c;
  outline: 0;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(92, 184, 92, 0.6);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(92, 184, 92, 0.6);
}
input.form-control::-webkit-input-placeholder { /* WebKit, Blink, Edge */
  color: #101010;
}
span.help {
  display: block;
  font-size: 11px;
  margin-top: 5px;
  color: #d9534f;
  float: right;
}
.select2-selection.select2-selection--multiple {
  border: 0px !important;
  background-color: #303030;
  color: white;
}
li.select2-selection__choice {
  background-color: #303030 !important;
}
span.select2-selection__choice__remove:hover {
  color: #d65652 !important;
}
.select2-dropdown.select2-dropdown--below {
  background-color: #303030;
  color: white;
  border-color: #141414;
}
.select2-results__option[aria-selected=true] {
  background-color: #111 !important;
}
span.select2-container--focus.select2-container--open {
  border-color: #66afe9 !important;
  outline: 0 !important;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(102, 175, 233, .6) !important;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(102, 175, 233, .6) !important;
}
span.select2 {
  border: 1px solid #141414 !important;
  border-radius: 4px;
}
.awe-borderline-gradient-animated {
  width: 100%;
  height: 1px;
  background: -webkit-gradient(linear, right top, left top, from(#df094f), to(#b32d8e));
  background: linear-gradient(270deg, #df094f, #b32d8e);
  background-size: 400% 400%;

  -webkit-animation: AweBorderlineAnimationName 10s ease infinite;
  animation: AweBorderlineAnimationName 10s ease infinite;
}
@-webkit-keyframes AweBorderlineAnimationName {
0% {
    background-position: 0% 50%
}
50% {
    background-position: 100% 51%
}
100% {
    background-position: 0% 50%
}
}
@keyframes AweBorderlineAnimationName {
0% {
    background-position: 0% 50%
}
50% {
    background-position: 100% 51%
}
100% {
    background-position: 0% 50%
}
}
.fade-enter-active {
  transition: opacity .5s;
  -webkit-transition: opacity .5s;
  opacity: 1;
}
.fade-enter {
  opacity: 0;
}
.fade-leave {
  display: none;
}
.fade-leave-active {
  display: none;
}
.scale-enter-active {
}
.scale-enter {
}
.scale-leave {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
}
.scale-leave-active {
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
  -webkit-transition: transform 0.5s;
  -webkit-transform: scale(0, 0);
          transform: scale(0, 0);
  z-index: 20;
}
.m-t-20 {
