
.nav-title {
  float: left;
}
.nav-awe {
  max-height: 100%;
}
.navbar-brand {
  height: 50px;
  padding: 15px 0px 0px 0px;
  font-size: 18px;
  line-height: 20px;
}
.nav-buttons {
  float: right;
  height: 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 12px;
  padding-bottom: 13px;
}
.navbar-header {
  width: 100%;
}
.nav-buttons .awe-button {
  color: white;
  cursor: pointer;
  padding-left: 15px;
  padding-right: 15px;
  font-size: 14px;
  line-height: 25px;
  vertical-align: middle;
  border-right: 1px solid rgba(255,255,255,0.1);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.nav-buttons .awe-button:last-of-type {
  border-right: 0px solid;
}
.nav-buttons .awe-button:hover {
  color: #ccc;
}
.awe-button .profile-img {
  width: 25px;
  height: 25px;
  display: inline-block;
  margin-right: 5px;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
}
.awe-menu-bars-button {
  float: left;
  height: 50px;
  padding: 15px 15px 0px 15px;
  font-size: 18px;
  line-height: 20px;
  color: #fff;
  cursor: pointer;
}
.awe-menu-bars-button:hover {
  color: #ccc;
}
.awe-left-menu {
  display: none;
  max-height: calc(100% - 51px);
  height: calc(100% - 51px);
  overflow-x: hidden;
  overflow-y: auto;
}
.awe-left-menu.awe-show-menu {
  display: block;
}
.awe-left-menu ul {
  padding: 15px 15px 0px 15px;
  display: block;
}
.awe-left-menu ul li {
  list-style: none;
  padding-top: 7px;
  padding-bottom: 7px;
  text-align: left;
  color: #fff;
  display: -webkit-box;
  cursor: pointer;
}
.awe-left-menu ul li:hover {
  color: #ccc;
}
.awe-left-menu-icon {
  margin-right: 10px;
}
.navbar.awe-show-menu {
  height: 100%;
}
@media screen and (max-width: 510px) {
.nav-buttons {
    display: none;
}
}
@media screen and (min-width: 511px) {
.awe-menu-bars-button {
    display: none;
}
.awe-left-menu.awe-show-menu {
    display: none;
}
.navbar.awe-show-menu {
    height: unset;
}
}
@media screen and (min-width: 768px) {
.navbar-brand {
    padding: 15px 15px;
}
}



