
.icon-image {
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
}
.modal-header h3 {
  margin-top: 0;
  color: #42b983;
}
.vue-base64-file-upload.form-control {
  height: auto;
  background: none;
  border: none;
  padding: 0px;
}
.vue-base64-file-upload-wrapper input {
  background-color: #303030;
  color: white;
  display: block;
  border: 1px solid #141414;
  border-radius: 4px;
  width: 100%;
  height: 34px;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
}




