
.awe-widget-body {
  width: 330px;
  background-color: #1c2232;
  /* margin-left: 24px; */
  /* margin-top: 24px; */
  float: left;
  border-right: 3px solid #161a25;
  height: 100%;
  display: inline;

  -webkit-animation: initWidget 0.3s ease;
  animation: initWidget 0.3s ease;
}
.awe-scroller {
  overflow-y: scroll;
  height: 100%;
  padding: 0px;
  background-color: #1c2232;
  width: 327px;
  height: 100% !important;
}
.awe-scroller::-webkit-scrollbar {
  width: 5px;
  height: 12px;
}
.awe-scroller::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0);
}
.awe-scroller::-webkit-scrollbar-thumb {
  background: #555;
  visibility: hidden;
}
.awe-scroller:hover::-webkit-scrollbar-thumb {
  visibility: visible;
}
.awe-widget-loading{
  position: -webkit-sticky;
  position: sticky;
  bottom: 0px;
  height: 30px;
  padding: 5px;
  background-color: rgba(0,0,0,0.3);
}
@media screen and (max-width: 510px) {
.awe-widget-body {
    width: 100%;
}
.awe-widget-body .awe-scroller {
    width: 100%;
}
}



